   color: @error-color-hover;\n    }\n  }\n\n  &&-disabled {\n    color: @disabled-color;\n    cursor: not-allowed;\n    user-select: none;\n  }\n\n  // Tag\n  div&,\n  p {\n    .typography-paragraph();\n  }\n\n  h1&,\n  div&-h1,\n  div&-h1 > textarea,\n  h1 {\n    .typography-title-1();\n  }\n\n  h2&,\n  div&-h2,\n  div&-h2 > textarea,\n  h2 {\n    .typography-title-2();\n  }\n\n  h3&,\n  div&-h3,\n  div&-h3 > textarea,\n  h3 {\n    .typography-title-3();\n  }\n\n  h4&,\n  div&-h4,\n  div&-h4 > textarea,\n  h4 {\n    .typography-title-4();\n  }\n\n  h5&,\n  div&-h5,\n  div&-h5 > textarea,\n  h5 {\n    .typography-title-5();\n  }\n\n  h1&,\n  h2&,\n  h3&,\n  h4&,\n  h5& {\n    .@{typography-prefix-cls} + & {\n      margin-top: @typography-title-margin-top;\n    }\n  }\n\n  div,\n  ul,\n  li,\n  p,\n  h1,\n  h2,\n  h3,\n  h4,\n  h5 {\n    + h1,\n    + h2,\n    + h3,\n    + h4,\n    + h5 {\n      margin-top: @typography-title-margin-top;\n    }\n  }\n\n  a&-ellipsis,\n  span&-ellipsis {\n    display: inline-block;\n    max-width: 100%;\n  }\n\n  a&,\n  a {\n    .operation-unit();\n    text-decoration: @link-decoration;\n\n    &:active,\n    &:hover {\n      text-decoration: @link-hover-decoration;\n    }\n\n    &[disabled],\n    &.@{typography-prefix-cls}-disabled {\n      color: @disabled-color;\n      cursor: not-allowed;\n\n      &:active,\n      &:hover {\n        color: @disabled-color;\n      }\n\n      &:active {\n        pointer-events: none;\n      }\n    }\n  }\n\n  code {\n    margin: 0 0.2em;\n    padding: 0.2em 0.4em 0.1em;\n    font-size: 85%;\n    background: rgba(150, 150, 150, 0.1);\n    border: 1px solid rgba(100, 100, 100, 0.2);\n    border-radius: 3px;\n  }\n\n  kbd {\n    margin: 0 0.2em;\n    padding: 0.15em 0.4em 0.1em;\n    font-size: 90%;\n    background: rgba(150, 150, 150, 0.06);\n    border: 1px solid rgba(100, 100, 100, 0.2);\n    border-bottom-width: 2px;\n    border-radius: 3px;\n  }\n\n  mark {\n    padding: 0;\n    background-color: @g